    getOtherAgendas(timestamp, day) {
        let timeS1 = mergedData.length > 0 ? mergedData[0].shouldDateTime : d.getTime(),
            timeS2 = allDayData.length > 0 ? allDayData[0].shouldDateTime : d.getTime(),
            timeE1 = mergedData.length > 0 ? mergedData[mergedData.length - 1].shouldDateTime : d.getTime(),
            timeE2 = allDayData.length > 0 ? mergedData[allDayData.length - 1].shouldDateTime : d.getTime();
        let timeS = timeS1 > timeS2 ? timeS2 : timeS1,
            timeE = timeE1 > timeE2 ? timeE1 : timeE2;
        let length = day * 24 * 60 * 60 * 1000,
            before = timestamp - timeS,
            after = timeE - timestamp;

        if (before < length && before > 0) {
            if (isIOS) {
                if (window.webkit !== undefined) {
                    console.log(window.webkit);
                    window.webkit.messageHandlers.getAgendaStartTimestamp.postMessage({ "startTime": before });
                }
            } else if (isAndroid) {
                window.js.getAgendaStartTimestamp(before);
            }
        } else if (after < length && after > 0) {
            if (isIOS) {
                if (window.webkit !== undefined) {
                    console.log(window.webkit);
                    window.webkit.messageHandlers.getAgendaStartTimestamp.postMessage({ "startTime": after });
                }
            } else if (isAndroid) {
                window.js.getAgendaStartTimestamp(after);
            }
        }
    },
    imageUrlIsExist(url) {
        var ImgObj = new Image(); //判断图片是否存在  
        ImgObj.src = url;
        //存在图片
        if (ImgObj.fileSize > 0 || (ImgObj.width > 0 && ImgObj.height > 0)) {
            return true;
        } else {
            return false;
        }

    }
