<template>
  <div>
    <div id="loadingContainer" v-if="!ready">
      <svg
        style="
          position: absolute;
          top: 50%;
          left: 50%;
          width: 48px;
          height: 48px;
          transform: translate(-50%, -50%);
        "
        version="1.1"
        id="L9"
        x="0px"
        y="0px"
        viewBox="0 0 100 100"
        enable-background="new 0 0 0 0"
        xml:space="preserve"
      >
        <path
          fill="#71d9c2"
          d="M73,50c0-12.7-10.3-23-23-23S27,37.3,27,50 M30.9,50c0-10.5,8.5-19.1,19.1-19.1S69.1,39.5,69.1,50"
          transform="rotate(188.223 50 50)"
        >
          <animateTransform
            attributeName="transform"
            attributeType="XML"
            type="rotate"
            dur="1s"
            from="0 50 50"
            to="360 50 50"
            repeatCount="indefinite"
          ></animateTransform>
        </path>
      </svg>
    </div>
    <div id="container" v-else>
      <div id="topYM">
        <div id="topYear"></div>
        <div id="topMonth"></div>
      </div>
      <div id="top">
        <div id="topContainer"></div>
      </div>
      <div id="bottom">
        <div id="left"></div>
        <div id="middle">
          <div id="middleContainer"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Calendar from "@/assets/js/calendar.js";
import calendarTools from "@/assets/js/calendar.tools.js";
import lessonTools from "@/assets/js/lesson.tools.js";
export default {
  data() {
    return {
      ready: true,
    };
  },
  mounted() {
    let d = new Date();
    let currentYear = d.getFullYear();
    let currentMonth = d.getMonth() + 1;
    let currentDate = d.getDate();
    let currentFormat = lessonTools.getFormatYYMMDD(d);
    let currentTimestamp = new Date(currentFormat).getTime();
    let lessonDays = 3;
    console.log("currentFormat: ", currentFormat);
    let firstFormatStr = "", lastFormatStr = ""

    let movable = true,
      selectedDayClickable = true;
    let originTimeLength = 60;
    // 15、30、45、60、75、90、105、120
    let originGridCount = originTimeLength / 15;
    let startIndex = 24,
      endIndex = 96 - originGridCount;

    let data = [],
      mergedData = [],
      allDayData = [];
    let isLoaded = false,
      startFromTimestamp = d.getTime();

    let testData = [
      {
        id: "",
        url:
          "https://storage.googleapis.com/tunekey-2019.appspot.com/images/user_avatars/YVaIJjT5CNRTOIyVBB0ugYcB57M2.jpg?GoogleAccessId=tunekey-2019%40tunekey-2019.iam.gserviceaccount.com&Expires=4102444800&Signature=JIPPpVGC3oLEUbS%2BSurSTHHg%2BtzRchAELU2X4FAxRc7k2gqPwmbjkFO%2FxpmzObwhro0KvdA3iUkH8uBwjdBEnHFYayDUgrG78vsiXGRoxCdNoM46mwWuXq5A9StFkvcZ%2BRN4Cew4MQdoDNd1%2Fp1y4%2BoWYgKhMpfMWXNwy8sgWctw%2FOQSR7qK7jqFrzdIf6VW4%2FmLDHySU4SN8YEAh1wP69MMomss2EMRJKch%2FQIm2C%2F1g%2Bm1LWeB3aMsiv5I2qC%2F6zkgJRrsoK0Zkc6ZXRfraFuf9ks%2FNA3XNhU8PEKfYkq3lzGgcdQkXOi5p0KYNOg9Pmf9UZIl6yLB8nsshvbYuA%3D%3D",
        isOverDay: false,
        shouldDateTime: new Date("2020/8/3 14:00").getTime(),
        shouldTimeLength: 60,
        type: 0, // group
        name: "Standard lesson", // lesson name
      },
    ];

    function getAgenda(dataFromClient, startTimestamp, minuteLength) {
      isLoaded = true;
      if (minuteLength) {
        originTimeLength = minuteLength;
        originGridCount = originTimeLength / 15;
      }
      if (startTimestamp) {
        startFromTimestamp = startTimestamp;
      }
      startFromTimestamp = startTimestamp;
      updateData(dataFromClient);
    }

    function updateData(dataFromClient) {
      data = JSON.parse(JSON.stringify(dataFromClient));
      lessonTools.addData(0, data, mergedData, allDayData, currentTimestamp);
      // consoleLog(data);
    }

    getAgenda(testData, 0, 120);

    /* start ------ */

    let w,
      marginLeft,
      moveType = 0; // 0 静止，1 上下，2 左右

    lessonTools.init(0, data, startFromTimestamp, mergedData, allDayData, lessonDays, firstFormatStr, lastFormatStr, currentFormat, currentTimestamp); // 灰
    w = $(".middle-day").eq(0).width();
    marginLeft = parseFloat($("#middleContainer").css("marginLeft"));
    $("#loadingContainer").remove();
    lessonTools.addCurrentDayMark();

    touch.on("#middle", "touchstart", function (ev) {
      // ev.preventDefault();
    });

    touch.on("#top", "touchstart", function (ev) {
      // ev.preventDefault();
    });

    touch.on("#middle", "swiping", function (ev) {
      if (moveType === 0) {
        moveType = ev.direction === "left" || ev.direction === "right" ? 2 : 1;
      }
      if (moveType === 2) {
        if (lessonTools.isHorizontal(ev) && movable) {
          moving(ev.distanceX);
        }
      }
    });

    touch.on("#top", "swiping", function (ev) {
      if (moveType === 0) {
        moveType = ev.direction === "left" || ev.direction === "right" ? 2 : 1;
      }
      if (moveType === 2) {
        if (lessonTools.isHorizontal(ev) && movable) {
          moving(ev.distanceX);
        }
      }
    });

    touch.on("#middle", "swipeend", function (ev) {
      if (movable) {
        if (moveType === 2) {
          moveEnd(ev.distanceX);
        }
        moveType = 0;
      }
    });

    touch.on("#top", "swipeend", function (ev) {
      if (movable) {
        if (moveType === 2) {
          moveEnd(ev.distanceX);
        }
        moveType = 0;
      }
    });

    function moveEnd(distanceX) {
      $("#bottom").css("overflow", "auto");
      let isLeft = distanceX > 0 ? false : true;
      let dis = Math.abs(distanceX);
      let isMoved1 = dis >= w / 2 && dis < w * 1.5,
        isMoved2 = dis >= w * 1.5 && dis < w * 2.5,
        isMoved3 = dis >= w * 2.5;

      if (isMoved1) {
        if (!isLeft) {
          marginLeft = marginLeft + w;
        } else {
          marginLeft = marginLeft - w;
        }
      } else if (isMoved2) {
        if (!isLeft) {
          marginLeft = marginLeft + w * 2;
        } else {
          marginLeft = marginLeft - w * 2;
        }
      } else if (isMoved3) {
        if (!isLeft) {
          marginLeft = marginLeft + w * 3;
        } else {
          marginLeft = marginLeft - w * 3;
        }
      }

      $("#middleContainer").animate({
        marginLeft: marginLeft,
      });
      $("#topContainer").animate(
        {
          marginLeft: marginLeft,
        },
        function () {
          if (isMoved1) {
            // length, isLeft, yymmdd, dayType
            lessonTools.updateTableAndDay(
              1,
              isLeft,
              isLeft ? lastFormatStr : firstFormatStr,
              3
            );

            updateMarginLeft();
          }

          if (isMoved2) {
            // length, isLeft, yymmdd, dayType
            lessonTools.updateTableAndDay(
              2,
              isLeft,
              isLeft ? lastFormatStr : firstFormatStr,
              3
            );

            updateMarginLeft();
          }

          if (isMoved3) {
            lessonTools.updateTableAndDay(
              3,
              isLeft,
              isLeft ? lastFormatStr : firstFormatStr,
              3
            );

            updateMarginLeft();
          }
        }
      );

      function updateMarginLeft() {
        marginLeft = -w * 3;
        $("#middleContainer").css("marginLeft", marginLeft);
        $("#topContainer").css("marginLeft", marginLeft);
        // $('#bottom').animate({
        //     scrollTop: 0
        // });

        let ymd = $(".day.day-3")
            .eq($(".day.day-3").length / 3)
            .data("ymd"),
          timestamp = new Date(ymd).getTime() / 1000;

        getAgenda(data);
      }
    }

    function moving(distanceX) {
      // console.log("moving: ", distanceX);
      $("#middleContainer").css("marginLeft", marginLeft + distanceX);
      $("#topContainer").css("marginLeft", marginLeft + distanceX);
      $("#bottom").css("overflow", "hidden");
    }
    /* end ------ */

    $(document).on("click", ".middle-day-time", function (ev) {
      // consoleLog("click 1");
      let el = $(this);
      let dayIndex = el.parent().index(),
        timeIndex = el.index(),
        yymmdd = $(".day").eq(dayIndex).data("ymd"),
        d = new Date(yymmdd),
        timeStart = d.getTime() + timeIndex * 15 * 60 * 1000,
        timeEnd = d.getTime() + (timeIndex + originGridCount) * 15 * 60 * 1000,
        timeStartHour = new Date(timeStart).getHours(),
        timeEndHour = new Date(timeEnd).getHours();

      let childrenCount = el.children().length;

      // consoleLog("click 2")

      let startHourNumber =
        timeIndex % 4 == 0
          ? timeStartHour
          : (timeIndex - 1) % 4 == 0
          ? timeStartHour + 0.25
          : (timeIndex - 2) % 4 == 0
          ? timeStartHour + 0.5
          : timeStartHour + 0.75;
      let endHourNumber =
        timeIndex % 4 == 0
          ? timeEndHour
          : (timeIndex - 1) % 4 == 0
          ? timeEndHour + 0.25
          : (timeIndex - 2) % 4 == 0
          ? timeEndHour + 0.5
          : timeEndHour + 0.75;

      //   consoleLog("click 3 - " + yymmdd + "-" + startHourNumber + "-" + endHourNumber)
      //consoleLog("click 4 - " + timeStartHour + "-" + timeEndHour)

      let timestamp =
        new Date(yymmdd).getTime() + startHourNumber * 60 * 60 * 1000;

      //  consoleLog("click 5 - " + new Date(timestamp));

      $(".selected-day-time").remove();

      if (childrenCount > 0) {
        for (let i = 0; i < childrenCount; i++) {
          if (el.children().eq(i).hasClass("middle-day-event-gray")) {
            el.children().eq(0).append("<div class='selected-day-time'></div>");
            break;
          } else {
            if (i == childrenCount - 1) {
              el.append("<div class='selected-day-time'></div>");
            }
          }
        }
      } else {
        el.append("<div class='selected-day-time'></div>");
      }

      if (timeStart < new Date().getTime()) {
        // consoleLog("click 6 - " + childrenCount)
        if (childrenCount > 0) {
          if (
            childrenCount == 1 &&
            $(".selected-day-time").eq(0).prev().attr("id") == "currentTimeMark"
          ) {
            $(".selected-day-time").eq(0).addClass("selected-past-day-time");
            $(".selected-day-time")
              .eq(0)
              .css("height", originGridCount * 22 - 4);
            // 此时寻找最近的可用时间
            lessonTools.findRecentAvailableTime($(".selected-day-time").eq(0));
            //consoleLog("click 22")
          } else {
            // consoleLog("click 23")
            $(".selected-day-time").eq(0).addClass("selected-event-day-time");
          }
        } else {
          // consoleLog("click 25")
          $(".selected-day-time").eq(0).addClass("selected-past-day-time");
          $(".selected-day-time")
            .eq(0)
            .css("height", originGridCount * 22 - 4);
          // 此时寻找最近的可用时间
          lessonTools.findRecentAvailableTime($(".selected-day-time").eq(0));

          // consoleLog("click 26")
        }
      } else {
        //    consoleLog("click 27")
        if (childrenCount > 0) {
          $(".selected-day-time").eq(0).addClass("selected-event-day-time");
        } else {
          $(".selected-day-time")
            .eq(0)
            .css("height", originGridCount * 22 - 4);
        }

        // 此时执行规避冲突的操作
        lessonTools.avoidConflictTime($(".selected-day-time").eq(0));
        // consoleLog("click 40")
      }
    });

    $(document).on("click", ".selected-day-time", function (ev) {
      ev.stopPropagation();
      if (selectedDayClickable) {
        if ($(this).hasClass("selected-past-day-time")) {
          console.log("past");
        } else if ($(this).hasClass("selected-event-day-time")) {
          console.log("has class");
        } else {
          console.log("future empty");
        }
      }
    });
  },
};
</script>
<style>
@import "../../../assets/css/nucleo/css/lesson.days.css";
</style>
